//     content: "";
//     width: 50%;
//     height: 100%;
//     top: 0;
//     background: yellow;
//     border-radius: 9px;
//     z-index: -1;
//     transition: 0.2s;
// }

.turn.left::after {
    left: 0;
}

.turn.left span.o {
    border-color: #eee;
}
.turn.right span.x::before,
.turn.right span.x::after {
    background: #eee;
}
.turn.right::after {
    left: 50%;
}

.winner {
    position: absolute;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    display: grid;
    place-items: center;
    background-color: rgba(0, 0, 0, 0.7);
}
.winner .text {
    background: #111;
    border: 2px solid #eee;
    padding: 20px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

.winner .text h2 {
    font-size: 2.5em;
    margin: 15px 0;
    color: $arenaLightBlue;
    font-family: 'CamptonBold', 'sans-serif';
}

.winner .win {
    margin: 0 auto;
    width: fit-content;
    //border: 2px solid #eee;
    border-radius: 10px;
    display: flex;
    gap: 15px;
}
